.content-flex {
  background: #f5f6fb;
  display: flex;
  flex-direction: column;
}
.content-header {
  text-align: center;
}
.content-h100 {
  flex-grow: 3;
}
.content-charts {
  background: white;
  margin: 10px;
  padding: 10px;
}
.content-layout {
  display: grid;
  grid-template-columns: 220px auto;
  height: auto;
}
.content-sidebar {
  background: white;
  display: flex;
  flex-direction: column;
  margin: 0 0 10px 10px;
  padding: 10px;
}
.content-body {
  background: white;
  height: 100%;
  margin: 0 10px 10px 10px;
  min-height: 100%;
  padding: 10px;
}
.content-row {
  display: flex;
  flex-direction: row;
}
.content-el {
  margin: 0 10px 10px 0;
}

.btnUpDownGroup {
  display: inline-block; 
  margin-right: 5px;
}
.btnUpDownGroup input {
  width: 4em;
}


.progressbar {
  background-color: black;
  border-radius: 13px;
  /* (height of inner div) / 2 + padding */
  margin: 5px;
  padding: 3px;
}

.progressbar>div {
  background-color: orange;
  border-radius: 10px;
  color: white;
  height: 20px;
  text-align: center;
}