html,
body,
button,
input {
  font-family: sans-serif;
  font-size: 14px;
}
body {
  margin: 0;
  padding: 0;
}

/* Hide number imput arrows for Chrome, Safari, Edge, Opera */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Hide number imput arrows for Firefox */
input[type=number] {
  -moz-appearance: textfield;
}
